#confirmDiv{
  padding: 7%;
}
#chooseDiv{
  padding: 7%;
}
#infoDiv{
  padding: 5%;
}
#filingStatusMainDiv{
  padding: 5%;
}
#formLabel{
  color: brown;
  font-size: x-large;
  font-family: Arial;
}
hr{
  padding-bottom: 1%;
}
#filingLabel{
  font-size: large;
  font-family: Arial;
  padding: 2%;
}
#backBtn{
  background-color: white;
  color: rgb(0, 102, 255);
  border: 1px solid rgb(0, 102, 255);;
}
label{
  font-size: medium;
  font-family: Arial;
}
input[type=radio] {
  height:20px; 
  margin-top: -1px;
  margin-right: 7px;
  width:20px; 
  vertical-align: middle;
}
input[type=file]{
  color: rgb(0, 81, 255);
}
#confirmlb{
  color: rgb(39, 202, 80);
  margin-left: 5px;
  padding-top: 2%;
  font-size: x-large;
}
#heavyHr{
  border: none;
  height: 1px;
  margin-top: 2px;
  /* Set the hr color */
  color: rgb(180, 180, 180); /* old IE */
  background-color: rgb(180, 180, 180);
}

 
@media (max-width:400px){
    #formLabel{
      color: brown;
      font-size: large;
      font-family: Arial;
    }
    .chooseLabels{
     font-size: small; 
    }
    .taxPayerLabels{
      
      font-size: 11px;
    }
    .taxInput{
      width: 130px !important;
      position: relative;
      margin-right: 15px;
      
      height: 30px !important;
    }
    .taxInput2{
      margin-right: 23px;
    }

    #filingStatusMainDiv{
      padding: 0%;
      height: 600px;
      width: 100%;
    }
    #chooseDiv{
      padding: 0%;
      height: 250px;
    }
    #confirmDiv{
      height: 600px;
      padding: 0%;
    }
    #infoDiv{
      padding: 0%;
      height: 1000px;
    }
    #nextBtn{
      width: 105px;
      font-size: xx-small;
    }
    #manuallyBtn{
      font-size: x-small;
    }
    #backBtn{
      width: 90px;
      font-size: xx-small;
    }

  }
 
